html, body { height: 100%; }

.footer-box {
    width: 340px;
    height: 60px;
    background-color: lightslategray;
    border-radius: 10px;
    margin: 20px;
}

.total {
    width: 150px;
    text-align: left;
    position: relative;
    left: 20px;
    top: 6px;
}

.checkout-button {
    margin: 10px;
    width: 75px;
    height: 20px;
    border-radius: 3em;
    background-color: transparent;
    box-shadow: 2px 2px 4px;
    font-size: small;
    position: relative;
    top: 10px;
}

.popup-content {  
    margin: auto;
    background: lightslategray;
    width: 50%;
    padding: 5px;
    border-radius: 10px;
    border-color: black;
}

.order-text {
    font-weight: bolder;
}