html, body { height: 100%; }

.menu-parent {
    width: 340px;
    height: 170px;
    margin: 20px;
    border-radius: 10px;
    background-color: antiquewhite;
}

.image {
    width: 150px;
    height: 150px;
    margin: 10px;
    border-radius: 10px;
}

.item {
    margin: 10px;
    height: 80px;
}

.pricing-items {
    margin: 10px;
}

.item-name {
    font-weight: bold;
    font-size: large;
    margin-top: 0px;
}

.item-desc {
    margin: auto;
    color: gray;
}

.price {
    height: 30px;
    width: 50px;
    margin-right: 10px;
}

.add {
    width: 80px;
    height: 30px;
    margin-top: 14px;
    margin-right: 25px;
    position: relative;
    bottom: 3px;
}

.state-button {
    margin: 2px;
    width: 20px;
    height: 20px;
    border-radius: 3em;
    background-color: transparent;
    box-shadow: 2px 2px 4px;
    font-size: small;
}

.count {
    position: relative;
    bottom: 11px;
    text-align: center;
}

